.card-list {
    width: 95vw;
    margin: 45rem auto;
    display: grid;
    grid-template-columns: repeat(auto-fit, 40rem);
    grid-gap: 2rem;
    -ms-flex-pack: center;
        justify-content: center;
    margin-bottom: 10rem;
}






